@import '../../../scss/mixin';

.connect__wallet{
  @include display-block;
  @include gradient-bg2(#E41166 ,#F3AC27);
  @include psuedo-bg(1px,7px,#222227);
  width: max-content;
  border-radius: 8px;
  p{
    @include flex-row;
    @include gradient-text4(#F0FEC5,#F3AC27);
    font:700 16px/18px 'Inter', sans-serif;
    padding: 5px 8px;
    .ball__pulse{
      margin-left: 4px;
      i{
        width: 5px;
        height: 5px;
      }
    }
  }
  @media only screen and (max-width : 768px){
    @include psuedo-bg(1px,5px,#222227);
    border-radius: 6px;
    p{
      font:600 14px/16px 'Inter', sans-serif;
    }
  }


}

.profile__wallet{
  @include flex-row;
  // background: #2F425F;
  @include gradient-bg2(#E41166 ,#F3AC27);
  @include psuedo-bg(1px,7px,#222227);
  width: max-content;
  max-width: 180px;
  border-radius: 8px;
  padding: 4px;
  h1{
   @include flex-row;
   height: auto;
   width: 24px;
   flex-shrink: 0;
   svg{
     @include display-block;
     height: auto;
     fill:#B1420A;
   }
  }
  p{
    @include display-block;
    @include ellipsis-text;
    font: 400 16px/18px 'Inter', sans-serif;
    color: #aaa;
    margin-left: 5px;
  }
  span{
    @include flex-row;
    width: 24px;
    height: auto;
    flex-shrink: 0;
    svg{
      @include display-block;
      height: auto;
      fill: #B1420A;
    }
  }

  &:hover{
    p{
      color: #ccc;
    }
    span svg{
      fill: #ccc;
    }
  }

  &.active{

    span svg{
      transform: rotate(180deg);
    }
  }

  @media only screen and (max-width : 576px){
    max-width: 150px;
    p{
      font: 400 14px/16px 'Inter', sans-serif;
      margin-left: 3px;
    }
    span{
      width: 20px;
    }
  }
}



.profile__popup{
  $br : 5px;
  $l-w : 30px;

  @include display-block;
  @include gradient-bg2(#512338,#1B532B);
  position: fixed;
  z-index: 1;
  top: 70px;
  right: -300px;
  max-width: 300px;
  // max-height: calc(100vh - 130px);
  padding: 2px;
  border-radius: $br;
  box-shadow: 0 0 10px 2px #000;
  transition: right .3s;
  overflow: hidden;

  &.active{
    right: 20px;
  }

  @media only screen and (max-width: 768px){
    top: 60px;
    right: -70%;
    width: 70%;
    &.active{
      right: 10px;
    }
  }

  @media only screen and (max-width: 300px){
    width: calc(100% - 20px);
    right: calc(20px - 100%);
    max-width: inherit;
  }

  .profile__popup__head{
    @include flex-row;
    @include justify-start;
    padding: 10px;
    flex-wrap: wrap;
    background: #18181B;
    border-radius: $br - 1 $br - 1 0 0;
    margin-bottom: 2px;
    box-shadow: inset 0 0 2px #000;

    .profile__popup__logo{
      @include flex-row;
      width: 30px;
      height: 30px;
      svg{
        @include display-block;
        height: auto;
        fill: #A29A58;
      }
    }
    .profile__popup__name{
      @include flex-row;
      @include justify-start;
      width: calc(100% - $l-w);
      height: 30px;
      p{
        @include display-block;
        @include gradient-text(#F3E784,#23A484);
        @include ellipsis-text;
        width: max-content;
        flex-shrink: 1;
        font: 600 20px/30px 'Inter', sans-serif;
        margin: 0 5px;
        color: #999;
      }
      input{
        @include display-block;
        flex-shrink: 1;
        font: 400 16px/18px 'Inter', sans-serif;
        margin: 0 5px;
        padding: 4px 6px;
        border: solid 2px #444;
        background: transparent;
        border-radius: 5px;
        color: #ccc;
      }
      span{
        @include display-block;
        width: max-content;
        flex-shrink: 0;
        cursor: pointer;
        svg{
          @include display-block;
          width: auto;
          height: 18px;
          fill:#115242;
        }
        &:hover{
          svg{fill:#fff;}
        }
      }

      &.profile__popup__name-edit{
        span{
          margin-right: 3px;
          &:last-child{
            margin-right: 0;
          }
          background: #1C2739;
          padding: 2px;
          border-radius: 4px;
          svg{fill:#999;}
          &:hover{
            background: #25344C;
            svg{fill:#ccc;}
          }
        }
      }

    }
    .profile__popup__address{
      @include flex-row;
      @include justify-start;
      cursor: pointer;
      margin: 5px 0 15px;
      p{
        @include display-block;
        @include ellipsis-text;
        @include gradient-text(#C2B869,#1A7B63);
        flex-shrink: 1;
        font:500 14px/16px 'Inter', sans-serif;
        margin-right: 5px;
      }
      span{
        @include display-block;
        width: max-content;
        flex-shrink: 0;

        &.active{
          &:after{
            position: absolute;
            display: block;
            bottom: calc(100% + 8px);
            right: 0;
            content: "copied";
            background: #333;
            padding: 4px 6px;
            border-radius: 3px;
            font: 400 12px/14px 'Inter', sans-serif;
            text-align: left;
            color:  #ccc;
            z-index: 1;
          }

          &:before {
            position: absolute;
            content: "";
            bottom: 100%;
            right: 4px;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-top: 8px solid #333;
          }
        }
        svg{
          @include display-block;
          width: auto;
          height: 16px;
          fill: #1A7B63;
        }
        &:hover{
          svg{fill: #fff;}
        }
      }
    }
    .profile__popup__balance{
      @include flex-row;
      @include justify-space-between;
      p{
        @include flex-row;
        width: max-content;
        font: 400 14px/20px 'Inter', sans-serif;
        color: #999;
        svg{
          display: block;
          width: 22px;
          height: auto;
          fill:#A29A58;
        }
        &:last-child{
          svg{
            fill:#0D3D31;
          }
        }
        span{
          @include display-block;
          width: max-content;
          font: 500 18px/20px 'Inter', sans-serif;
          margin: 0 2px;
          color: #bbb;
        }
      }
    }
    .profile__popup__btns{
      @include flex-row;
      @include justify-space-between;
      margin-top: 10px;
      .profile__popup__btn{
        @include display-block;
        width: calc(50% - 5px);
        font: 400 14px/16px 'Inter', sans-serif;
        color: #bbb;
        background: #1C2739;
        padding: 6px 4px;
        border-radius: 4px;
        &:hover{
          color: #fff;
          background: #25344C;
        }
      }
    }

  }
  .profile__popup__content{
    @include display-block;
    box-shadow: inset 0 0 2px #000;
    padding: 15px 10px;
    background: #18181B;
    margin-bottom: 2px;
    &:last-child{
      margin-bottom: 0;
      border-radius: 0 0 $br - 1 $br - 1;
    }
    ul{
      @include display-block;

      li{
        @include display-block;
        color: #999;
        background: #1a1a25;
        font: 400 14px/16px 'Inter', sans-serif;
        margin-bottom: 10px;
        padding: 6px 10px;
        cursor: pointer;
        &:last-child{
          margin-bottom: 0;
        }
        &:hover{
          color: #ccc;
        }
      }
    }
  }
}
