@import '../../../scss/mixin';


.history__tabs__panel{
  @include display-block;
  margin-bottom: 10px;

  .history__tabs__list{
    @include flex-row;
    @include justify-space-between;
    border-bottom: 2px solid #303030;

    li{
      @include display-block;
      width: auto;
      background: transparent;
      transition: background .4s;
      border-radius: 6px 6px 0 0;
      padding: 0 15px;
      span{
        @include display-block;
        width: max-content;
        cursor: pointer;
        text-align: center;
        color: #bbb;
        transition: color .4s;
        padding: 8px 0 6px;
        font: 400 18px/20px 'Inter', sans-serif;
      }
      &:hover{
        background: #202020;
        span{
          color: #ddd;
        }
      }
      &.active{
        background: #303030;
        span{
          @include gradient-text(#CCF9FA,#F3E49E,135deg);
          font-weight: 600;
        }
      }
    }
  }
  @media only screen and (max-width : 768px){
    .history__tabs__list{
      li{
        span{
          padding: 8px 0 6px;
          font: 400 16px/18px 'Inter', sans-serif;
        }
      }
    }
  }
  @media only screen and (max-width : 576px){
    @include scroll-bar3;
    overflow-x:auto;
    overflow-y: hidden;
    // margin-bottom: 5px;

    .history__tabs__list{
      width: max-content;
      min-width: 100%;
      margin-bottom: 5px;
      li{
        padding: 0 15px;
      }
    }
  }
}


.no__result__found{
  @include flex-row;
  margin-top: 20px;
  svg{
    @include display-block;
    width: 80px;
    height: auto;
    polyline,polygon{
      stroke: #333;
    }
  }
  p{
    @include display-block;
    width: max-content;
    color: #888;
    text-align: center;
    font: 500 18px/20px 'Inter', sans-serif;
    margin-left: 10px;
  }
  @media only screen and (max-width : 576px){
    margin-top: 10px;
    svg{
      width: 60px;
    }
    p{
      font: 500 16px/18px 'Inter', sans-serif;
      margin-left: 6px;
    }
  }
}



.bets__info{
  @include flex-row;
  @include justify-space-between;
  flex-wrap: wrap;
  margin-bottom: 10px;

  .bets__info__content{
    @include display-block;
    width: calc(50% - 6px);
    text-align: center;
    background: #202020;
    margin-bottom: 12px;
    padding: 8px 5px;

    --r : 8px;
    &:nth-child(1){
      border-radius: var(--r) 0 0 0;
    }
    &:nth-child(2){
      border-radius: 0 var(--r) 0 0;
    }
    &:nth-child(3){
      border-radius: 0 0 0 var(--r);
      margin-bottom: 0;
    }
    &:nth-child(4){
      border-radius: 0 0 var(--r) 0;
      margin-bottom: 0;
    }

    p{
      @include display-block;
      @include gradient-text4(#E8A561,#27C575);
      width: max-content;
      font: 600 16px/18px 'Inter', sans-serif;
      color: #aaa;
      margin: 0 auto 10px;
    }
    span{
      @include display-block;
      color: #aaa;
      font: 400 15px/16px 'Inter', sans-serif;

      &.gain{
        font-weight: 500;
        color: #00A300;
      }
      &.loss{
        font-weight: 500;
        color: #E33333;
      }
    }
  }

  &.bets__info-dummy{
    .bets__info__content span{
      margin: 0 auto;
      @include gradient-bg4(#3B3637,#7F7873);
      width: 90px;
      border-radius: 5px;
    }
  }


  @media only screen and (max-width : 576px){
    // margin-bottom: 20px;
    .bets__info__content{
      width: calc(50% - 4px);
      margin-bottom: 8px;
      padding: 6px 4px;
      --r : 5px;
      p{
        font: 600 15px/16px 'Inter', sans-serif;
        margin: 0 auto 8px;
      }
      span{
        font: 400 14px/16px 'Inter', sans-serif;
      }
    }
    &.bets__info-dummy{
      .bets__info__content span{
        width: 50%;
      }
    }
  }
}

.history__popup__table{
  @include display-block;
  margin: 0 auto;

  .history__popup__table__row{
    @include flex-row;
    @include justify-start;
    background: #202020;
    color: #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 8px 5px;
    cursor: pointer;
    &:last-child{
      margin-bottom: 0;
    }

    .history__popup__table__cell{
      @include flex-row;
      width: 25%;
      flex-shrink: 0;
      font:400 16px/18px 'Inter', sans-serif;
      padding: 0 2px;

      img{
        @include display-block;
        width: 16px;
        margin-right: 4px;
      }

      span{
        @include display-block;
        width: max-content;
        &.won{
          font-weight: 500;
          color: #59BD2B;
        }
        &.lost{
          font-weight: 500;
          color: #DC161F;
        }
        &.highlight{
            background: #303030;
            border-radius: 18px;
            font-size: 14px;
            padding: 2px 10px;
        }
      }
    }

    &.history__popup__table__row-head{
      background: #303030;
      .history__popup__table__cell{
        p{
          @include display-block;
          @include gradient-text4(#ACCDEE,#F4DCB6,0deg);
          width: max-content;
          font:700 16px/18px 'Inter', sans-serif;
        }
      }
    }
    &.history__popup__table__row-dummy{
      .history__popup__table__cell{
        span{
          @include gradient-bg4(#3B3637,#7F7873);
          width: 50%;
          border-radius: 5px;
        }
      }
    }
  }

  .history__popup__table__row__expand{
    @include display-block;
    background: #202020;
    color: #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 15px 20px;
    .history__popup__table__cell__expand{
      @include flex-row;
      @include justify-start;
      font:700 16px/18px 'Inter', sans-serif;
      color: #ccc;
      margin-bottom: 20px;
      &:last-child{
        margin-bottom: 0;
      }
      p{
        @include display-block;
        font:400 16px/18px 'Inter', sans-serif;
        width: 20%;
        color: #ACCDEE;
      }
      span,a{
        @include display-block;
        width: max-content;
        font:400 16px/18px 'Inter', sans-serif;
        color: #bbb;
        margin-left: 20px;
        &.green{
          font-weight: 500;
          color: #59BD2B;
        }
        &.red{
          font-weight: 500;
          color: #DC161F;
        }
      }
    }
  }


  &.history__popup__table2{
    .history__popup__table__row{
      .history__popup__table__cell{
        &:nth-child(1),&:nth-child(4){
          width: 30%;
          color: #bbb;
          font:400 15px/16px 'Inter', sans-serif;
        }
        &:nth-child(2){
          @include justify-start;
          width: 20%;
          img{
            margin-right: 6px;
          }
          .select__modal__selected{
            @include justify-start;
          }
        }
        &:nth-child(3){
          width: 20%;
        }
      }
    }
  }
  &.history__popup__table3{
    .history__popup__table__row{
      .history__popup__table__cell{
        width: 33.3%;
      }
    }
  }

  @media only screen and (max-width : 576px){
    .history__popup__table__row{
      margin-bottom: 8px;
      padding: 6px 4px;
      .history__popup__table__cell{
        font:400 14px/16px 'Inter', sans-serif;
        img{
          width: 14px;
          margin-right: 2px;
        }
        span{
          &.highlight{
              font-size: 13px;
              padding: 2px 8px;
          }
        }
      }

      &.history__popup__table__row-head{
        padding: 10px 4px;
        .history__popup__table__cell{
          p{
            width: max-content;
            font:500 14px/16px 'Inter', sans-serif;
          }
        }
      }
      &.history__popup__table__row-dummy{
        .history__popup__table__cell{
          span{
            width: 70%;
          }
        }
      }
    }

    .history__popup__table__row__expand{
      margin-bottom: 8px;
      padding: 10px 15px;
      .history__popup__table__cell__expand{
        margin-bottom: 15px;
        p{
          font:400 14px/16px 'Inter', sans-serif;
          width: 35%;
        }
        span,a{
          font:400 14px/16px 'Inter', sans-serif;
          margin-left: 10px;
        }
      }
    }

    &.history__popup__table2{
      .history__popup__table__row{
        .history__popup__table__cell{
          &:nth-child(1){
            width: 28%;
            font:400 12px/16px 'Inter', sans-serif;
          }
          &:nth-child(4){
            width: 22%;
            font:400 12px/16px 'Inter', sans-serif;
          }
          &:nth-child(2){
            width: 25%;
            img{
              margin-right: 4px;
            }
          }
          &:nth-child(3){
            width: 25%;
          }
        }
      }
    }
  }
}
