@import '../../scss/mixin';


.footer{
  @include display-block;
  padding: 20px 20px;
  h4{
    @include display-block;
    @include gradient-text3(#E2F6AD,#E01C38);
    width: max-content;
    margin: 0 auto;
    font:700 26px/30px 'Inter', sans-serif;
    color: #aaa;
    text-align: center;
  }
  .footer__social{
    @include flex-row;
    margin: 10px 0 20px;
    li{
      @include display-block;
      width: max-content;
      margin-right: 20px;
      &:last-child{
        margin-right: 0;
      }
      a{
        @include flex-row;
        font-size: 30px;
        color: #ccc;
        text-decoration: none;
      }
    }
  }

  .footer__info{
    @include display-block;

    p{
      @include display-block;
      font:400 14px/20px 'Inter', sans-serif;
      color: #aaa;
      text-align: justify;
      text-align-last: start;
      margin: 10px 0;
    }
  }
  .footer__terms{
    @include display-block;
    font:500 16px/18px 'Inter', sans-serif;
    color: #ccc;
    margin-top: 20px;
  }

  @media only screen and (max-width : 768px){
    padding: 10px 10px;
    h4{
      font:600 24px/28px 'Inter', sans-serif;
    }
    .footer__social{
      margin: 10px 0 10px;
      li{
        margin-right: 10px;
        a{
          font-size: 24px;
        }
      }
    }
    .footer__info{
      p{
        font:400 14px/18px 'Inter', sans-serif;
        margin: 6px 0;
      }
    }
    .footer__terms{
      font:500 14px/18px 'Inter', sans-serif;
      margin-top: 20px;
    }
  }
}
