@import '../../scss/mixin';

.main__panel{
  @include display-block;
  .main__panel__top{
    @include flex-row;
    // flex-direction: row-reverse;
    margin-top: 50px;
    align-items: stretch;
    .dice__panel{
      @include flex-column;
      width: 75%;
      min-height: calc(100vh - 50px);
      .dice__panel__top{
        @include flex-column;
        @include align-center;
        @include justify-center;
        flex: 1;
        padding: 20px 0;
        background: #0a0a0a ;
        background: radial-gradient(circle, #2a2a2a 0%, #202020 20%, #0a0a0a 85%);
      }
      .dice__panel__bottom{
        @include display-block;
        flex-shrink: 0;

        .dice__panel__bottom__body{
          @include flex-row;
          @include justify-space-between;
          @include align-start;
          width: 992px;
          margin: 0 auto;
          padding: 40px 0;

          .dice__panel__bottom__left{
            @include display-block;
            width: calc(60% - 15px);
          }
          .dice__panel__bottom__right{
            @include display-block;
            width: calc(40% - 15px);

            button{
              @include flex-row;
              background: #121e38;
              border-radius: 5px;
              font: 500 20px/24px 'Inter', sans-serif;
              color:  #aaa;
              padding: 10px 5px;
              margin: 20px 0 8px;
              &:hover{
                background: #0E1933;
              }
            }
          }
          @media only screen and (max-width : 1432px){
            // background: pink;
            width: 100%;
            padding: 30px 20px;
          }
          @media only screen and (max-width : 1200px){
            // background: maroon;
            padding: 20px 20px;
          }
          @media only screen and (max-width : 992px){
            // background: blue;
            padding: 30px 15px;
          }
          @media only screen and (max-width : 768px){
            // background: purple;
            padding: 20px 10px;
            .dice__panel__bottom__left{
              width: calc(50% - 10px);
            }
            .dice__panel__bottom__right{
              width: calc(50% - 10px);
            }
          }
          @media only screen and (max-width : 576px){
            // background: red;
            padding:10px 10px;
            flex-wrap: wrap;
            .dice__panel__bottom__left{
              width: 100%;
            }
            .dice__panel__bottom__right{
              width: 100%;
              margin-top: 15px;
            }
          }
        }
      }
    }

    .dice__right{
      @include display-block;
      width: 25%;
      min-width: 400px;
      // min-height: 100px;
      background: #0A0E15;
      // background: red;
    }




    @media only screen and (max-width : 992px){
      flex-wrap: wrap;
      align-items:normal;

      .dice__panel{
        // flex:none;
        width: 100%;
      }
      .dice__right{
        // flex:none;
        width: 100%;
        min-width: inherit;
        // min-height: 200px;
      }
    }
    @media only screen and (max-width : 576px){

      .dice__panel{
        min-height: inherit;
        .dice__panel__top{
          flex-shrink: 0;
        }
      }
    }
  }

}



.form__label{
  @include display-block;
  @include gradient-text2(#46EB80,#F31A46,225deg);
  font:500 18px/20px 'Inter', sans-serif;
}
.form__input{
  @include display-block;
  @include gradient-bg2(#2F425F,#02264F);
  margin: 10px 0 10px;
  padding: 2px;
  border-radius: 6px;
  input{
    @include display-block;
    font:400 24px/24px 'Inter', sans-serif;
    text-align: center;
    background: #18181B;
    color: #ccc;
    border-radius: 5px;
    padding: 4px 4px;

    &::placeholder{
      font:400 18px/24px 'Inter', sans-serif;
      color: #888;
    }
  }
}

.form__button{
  @include flex-row;
  font:400 16px/18px 'Inter', sans-serif;
  color: #bbb;
  background: #1C2739;
  padding: 10px 10px;
  border-radius: 6px;
  &:hover{
    color: #fff;
    background: #25344C;
  }
}



/************************/
.popup-info-panel{
  position: absolute;
  display: none;
  width: 80%;
  top: 20px;
  z-index: 3;
  text-align: center;
  left: 10%;
  border-radius: 6px;
  border: 3px groove #775311;
}
.popup-info-panel-head{
  padding: 6px 0;
  font: 400 16px popfont1;
  border-radius: 6px 6px 0 0 ;
  background: #ff6511;
  background: linear-gradient(to right, #ff6511, #ffae11);
}
.popup-info-panel-head span{
  float: right;
  margin-right: 10px;
  font: 500 20px popfont1;
  cursor: pointer;
}
.popup-info-panel-head span:hover{
  color: #ddd;

}
.popup-info-panel-body{
  padding: 10px 0;
  font: 400 16px popfont1;
  background: #df9c1f;
}

.popup-info-panel-body button{
  font: 400 22px popfont1;
  color: #3c2812;
  background: #e79f34 ;
  border: 2px inset #ce995d;
  box-shadow: inset 0 0 8px #764a2a;
  margin-bottom: 15px;
}
.popup-info-panel-body button:focus{
  transform: translateY(5px);
  box-shadow: 0 0 2px 2px #fff;
}

