}


.news-social{
  position: relative;
  display: block;
  width: 90%;
  margin: 10px 5%;
}
.rank-box{
  display: none;
      color: #fff;
      text-align: center;

}
.dum{
  position: relative;
  display: block;
  width: 10px;
  z-index: 1;
  height: 500px !important;
}
p.rank-head{
  margin-top: 10px;
