}
table.rankTable{
  position: relative;
  width: 100%;

  margin: 10px 0;
  text-align: center;
}
table.rankTable th{
  color: #0b0c22;
  font: 600 16px chatfont5;
  text-align: center;
}
table.rankTable thead{

  border-bottom: 3px groove #999;
}
table.rankTable tbody{
  min-height: 60vh;
}

table.rankTable tbody tr{
  width: 100%;
  color: #0b0c22;
  font: 600 14px chatfont5;
  border-bottom: 3px groove #999;
}

table.rankTable span{
  background: #DA4453;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #89216B, #DA4453);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #89216B, #DA4453); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
box-shadow: inset 0 0 2px #000;
  padding: 5px;
  width: 100% !important;
  border-radius: 5px;
  color: #ffffff;
  box-shadow: inset 0 0 5px #000;
  text-shadow: 1px 2px 4px #000;
}
table.rankTable tr:nth-of-type(1) span{
  font-size: 28px;
  padding: 5px 12px;
box-shadow: inset 0 0 8px #000;
text-shadow: 1px 2px 6px #000;

}
table.rankTable tr:nth-of-type(2) span{
  font-size: 25px;
  padding: 5px 10px;
box-shadow: inset 0 0 7px #000;
text-shadow: 1px 2px 4px #000;


}
table.rankTable tr:nth-of-type(3) span{
  font-size: 22px;
  padding: 5px 8px;
box-shadow: inset 0 0 6px #000;
text-shadow: 1px 2px 3px #000;

}
table.rankTable tr:nth-of-type(4) span{
  font-size: 20px;
  padding: 5px 8px;
box-shadow: inset 0 0 4px #000;
text-shadow: 1px 2px 3px #000;
}
table.rankTable tr:nth-of-type(5) span{
  font-size: 18px;
  padding: 5px 8px;
box-shadow: inset 0 0 3px #000;
text-shadow: 1px 2px 3px #000;

}
table.rankTable tr:nth-of-type(6) span{
  padding: 5px 8px;
}
table.rankTable tr:nth-of-type(7) span{
  padding: 5px 8px;
}
table.rankTable tr:nth-of-type(8) span{
  padding: 5px 8px;
}
table.rankTable tr:nth-of-type(9) span{
  padding: 5px 8px;
}
table.rankTable tr:nth-of-type(1) td{
  font-size: 28px;
  color: #872137;
}
table.rankTable tr:nth-of-type(2) td{
  font-size: 25px;
  color: #732133;
}
table.rankTable tr:nth-of-type(3) td{
  font-size: 22px;
  color: #43141e;
}
table.rankTable tr:nth-of-type(4) td{
  font-size: 20px;
  color: #2f0e15;
}
table.rankTable tr:nth-of-type(5) td{
  font-size: 18px;
  color: #240a10;
}

@media only screen and (max-width : 480px) {
  p.rank-head{
    font: 600 30px hemihead;
    text-shadow: 2px 4px 8px #000;
  }
  p.quoteForTop{
    font: 600 26px hemihead;
    text-shadow: 1px 3px 5px #000;
  }
  table.rankTable tr td:nth-of-type(1){
   width: 10% !important;
  }
  table.rankTable tr td:nth-of-type(2){
    width: 10px !important;
    overflow: hidden !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;

  }
  table.rankTable tr td:nth-of-type(3){
    width: 40%;
  }
  td.chopCellRank{
    max-width: 10px !important;
    overflow: hidden !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
    font-size: 0.8em;
  }
}


/**************/
.ref-box{
  display: none;
  text-align: center;
}
p.Refferal-head{
  margin-top: 10px;
  font: 600 36px hemihead;
  text-shadow: 2px 4px 8px #000;
}
.ref-box input{
  width: 40%;
  background: #ffe259;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffa751, #ffe259);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffa751, #ffe259); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  color: #0b0c22;
  border: 4px groove #b87f00;
  text-shadow: 0 1px 1px #bbb;
  cursor: not-allowed;
}
.myRefDiv{
  display: block;
  position: relative;
  width: 70%;
  margin: 10px auto;
}
.myRefDiv input{
  width: 70%;
}
.myRefDiv button{
  width: 28%;
  background: #e65c00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #F9D423, #e65c00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F9D423, #e65c00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 5px 0;
  color: #fff;
  font: 400 16px popfont1 ;
  border: 2px inset #ffa751;
  text-shadow: 0 0 6px #000;
}
.myRefDiv button:hover{
  background: -webkit-linear-gradient(to left, #F9D423, #e65c00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #F9D423, #e65c00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.refInfo{
  display: block;
  position: relative;
  width: 90%;
  margin: 50px auto;
}
.refInfo-item{
  display: block;
  position: relative;
  width: 45%;
  float: left;
}
.refInfo-item input{
  width: 100%;
}
.earnedByRef{
  display: block;
  position: relative;
  width: 45%;
  margin: 20px auto;
}
.earnedByRef input{
  width: 100%;
}
p.refDet{
  width: 70%;
  margin: 10px auto;

}
@media only screen and (max-width : 480px){
  .refInfo-item{
    float: none;
    margin: auto;
    width: 80%;
  }
  .earnedByRef{
    width: 72%;
  }
  p.Refferal-head{
    font-size: 30px;
    text-shadow: 2px 4px 8px #000;
  }
  p.quoteForTop{
    font: 400 20px chatfont4;
  }
  .myRefDiv {
    width: 90%
  }
  .myRefDiv input{
    width: 100%;
  }
  .myRefDiv button{
    margin-top: 10px;
    width: 40%;
  }

}


