@import '../../../scss/mixin';

.redirect__breadcrumb{
  @include flex-row;
  @include user-select-none;
  width: max-content;
  cursor: pointer;
  transition: border-color 0.4s;
  // border: solid 2px #1D3458;
  margin-bottom: 20px;

  svg{
    @include display-block;
    width: auto;
    fill: #335B9A;
    transition: fill 0.4s;
    height: 24px;
  }
  span{
    @include display-block;
    width: max-content;
    color: #999;
    border-bottom: solid 1px #1D3458;
    transition: color 0.4s,border-color 0.4s;
    font: 400 15px/16px 'Inter', sans-serif;
    margin-left: 3px;
  }
  &:hover{
    svg{
      fill:#718DB9;
    }
    span{
      color: #ccc;
      border-color: #335B9A;
    }
  }
}

.deposit__panel{
  @include display-block;

  .deposit__account__balance{
    @include flex-row;
    @include justify-space-between;
    margin-bottom: 10px;
    p{
      @include flex-row;
      width: max-content;
      font: 400 16px/20px 'Inter', sans-serif;
      color: #aaa;
      svg{
        display: block;
        width: 30px;
        height: auto;
        fill:#666;
      }
      span{
        @include display-block;
        width: max-content;
        font: 400 16px/20px 'Inter', sans-serif;
        margin: 0 2px 0 8px;
        color: #ccc;
      }
    }
  }

  .input__and__select{
    @include flex-row;
    border: solid 2px #1C2739;
    background: #18181B;
    padding: 2px;
    border-radius: 6px;
    input{
      @include display-block;
      font:400 24px/26px 'Inter', sans-serif;
      text-align: center;
      background: #18181B;
      color: #ccc;
      border-radius: 5px 0 0 5px;
      padding: 6px 6px;
      flex-shrink: 1;
      &::placeholder{
        font:400 18px/26px 'Inter', sans-serif;
        color: #888;
      }
    }
    .deposit__select{
      @include flex-row;
      width: max-content;
      background: #1C2739;
      padding: 4px 10px;
      border-radius: 6px;
      margin-right: 6px;
      flex-shrink: 0;
    }
  }

  .deposit__input__note{
    @include display-block;
    font: 400 15px/16px 'Inter', sans-serif;
    text-align: right;
    margin-top: 8px;
    color: #aaa;

  }
  .deposit__note{
    @include display-block;

    p{
      @include display-block;
      font:400 16px/22px 'Inter', sans-serif;
      color: #aaa;
      margin-bottom: 8px;

      span{
        @include display-block;
        display: inline;
        font-weight: 500;
        color: #E33333;
      }
    }
  }

  .form__button{
    font:500 18px/20px 'Inter', sans-serif;
    margin: 20px 0;
  }


  @media only screen and (max-width : 576px){
    .deposit__account__balance{
      p{
        font:400 14px/18px 'Inter', sans-serif;
        svg{
          width: 25px;
        }
        span{
          font:500 16px/18px 'Inter', sans-serif;
          margin: 0 2px 0 4px;
        }
      }
    }

    .deposit__input__note{
      font: 400 14px/16px 'Inter', sans-serif;
      margin-top: 6px;
    }
    .deposit__note{
      p{
        font:400 15px/20px 'Inter', sans-serif;
        margin-bottom: 6px;
      }
    }
    .form__button{
      font:400 18px/20px 'Inter', sans-serif;
      margin: 15px 0;
    }
  }
}


canvas{
  @include display-block;

}
