@import '../../scss/mixin';

@-webkit-keyframes opacity-effect {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.1) translateX(-50%);
            transform: scale(0.1) translateX(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1) translateX(-50%);
            transform: scale(1) translateX(-50%);
  }
}
@keyframes opacity-effect {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.1) translateX(-50%);
            transform: scale(0.1) translateX(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1) translateX(-50%);
            transform: scale(1) translateX(-50%);
  }
}

.select__modal{
  @include display-block;
  @include user-select-none;
  cursor: pointer;

  .select__modal__selected{
    @include flex-row;
    color:#aaa;
    &.active{
      svg{
        transform: rotate(180deg);
      }
    }
    p{
      @include display-block;
      @include gradient-text4(#ACCDEE,#F4DCB6,0deg);
      width: max-content;
      font:700 16px/18px 'Inter', sans-serif;
    }
    svg{
      display: block;
      width: 14px;
      height: auto;
      fill: #8BA6C1;
      margin-left: 5px;
      flex-shrink: 0;
    }
    &:hover{
      svg{
        fill: #F4DCB6;
      }
    }
  }

  .select__modal__options{
    @include display-block;
    width: max-content;
    position: absolute;
    z-index: 5;
    background: #303030;
    top: calc(100% + 10px);
    left: 50%;
    transform: translateX(-50%);
    margin: 0 auto;
    transform-origin: left top;
    @include animation(opacity-effect 0.25s ease-out);

    .select__modal__option{
      @include flex-row;
      @include justify-space-between;
      padding: 8px 6px;
      border-bottom: solid 1px #202020;
      &:last-child{
        border-bottom: none;
      }
      span{
        @include display-block;
        width: max-content;
        color: #ccc;
        font: 400 15px/16px 'Inter', sans-serif;
      }
      svg{
        display: block;
        width: 18px;
        height: auto;
        margin-left: 5px;
        stroke: transparent;

      }
      &.active{
        span{
          color: #ACCDEE;
        }
        svg{
          stroke: #ACCDEE;
        }
      }
      &:hover{
        span{
          color: #F4DCB6;
        }
      }
    }
  }

  @media only screen and (max-width : 576px){
    .select__modal__selected{
      p{
        font:500 14px/16px 'Inter', sans-serif;
      }
      svg{
        width: 12px;
        margin-left: 2px;
      }
    }
    .select__modal__options{
      top: calc(100% + 12px);
      .select__modal__option{
        padding: 6px 4px;
        span{
          font: 400 13px/16px 'Inter', sans-serif;
        }
        svg{
          width: 14px;
          margin-left: 2px;

        }
      }
    }
  }

}
