@import '../../../scss/mixin';

.dice__history__panel{
  @include display-block;
  padding: 0 20px;
  overflow: hidden;

  @media only screen and (max-width : 992px){
    padding: 0 15px;
  }
  @media only screen and (max-width : 768px){
    padding: 0 10px;
  }
}

.history__select{
  @include display-block;
  margin: 20px 0 10px;
  .history__select__selected{
    display: none;
  }
  .history__select__options{
    @include flex-row;
    @include space-evenly;
    background: #1E1E1E;
    border-radius: 10px 10px 0 0;

    li{
      @include display-block;
      width: auto;
      padding: 6px 0 4px;
      p{
        @include display-block;
        cursor: pointer;
        font: 400 16px/18px 'Inter', sans-serif;
        color: #aaa;
        padding-bottom: 3px;
        border-bottom: solid 1px transparent;
      }
      &:hover{
        p{
          color: #ccc;
        }
      }
      &.active{
        p{
          color: #ccc;
          border-color: #aaa;
        }
      }
    }
  }
  @media only screen and (max-width : 768px){
    margin: 10px 0 10px;
    .history__select__options{
      border-radius: 6px 6px 0 0;
      li{
        p{
          font: 400 15px/16px 'Inter', sans-serif;
        }
      }
    }
  }
  @media only screen and (max-width : 576px){
    margin: 10px 0 8px;
    .history__select__options{
      li{
        p{
          font: 400 14px/16px 'Inter', sans-serif;
        }
      }
    }
  }
}


.dice__history__table{
  @include display-block;
  background: #1E1E1E;
  padding: 10px;
  border-radius: 0 0 10px 10px;

  .dice__history__table__row{
    @include flex-row;
    @include justify-start;
    background: #0C0B0E;
    color: #aaa;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 8px 0;
    &:last-child{
      margin-bottom: 0;
    }

    .dice__history__table__cell{
      @include flex-row;
      width: 20%;
      flex-shrink: 0;
      font: 400 16px/18px 'Inter', sans-serif;
      padding: 0 2px;
      span{
        @include display-block;
        width: max-content;
        &.won{
          color: #4DA525;
        }
        &.lost{
          color: #DC161F;
        }
        &.dim{
          color: #666;
        }
        &.highlight{
          background: #1E1C1D;
          text-align: center;
          border-radius: 18px;
          font: 400 14px/18px 'Inter', sans-serif;
          padding: 2px 0;
          width: 90px;
        }
      }
      img{
        @include display-block;
        width: 20px;
        margin-right: 10px;
      }
      &:nth-child(1){
        color: #999;
        font:400 14px/18px 'Inter', sans-serif;
      }

      &:nth-child(4),&:nth-child(5){
        @include justify-start;
      }
    }

    &.dice__history__table__row-head{
      background: #1E1E1E;
      margin-bottom: 0;
      padding: 3px 0 8px;

      .dice__history__table__cell{
        p{
          @include display-block;
          color: #ACCDEE;
          // @include gradient-text4(#ACCDEE,#F4DCB6,0deg);
          width: max-content;
          font:400 14px/16px 'Inter', sans-serif;
        }
      }
    }
    &.dice__history__table__row-dummy{
      .dice__history__table__cell{
        span{
          @include gradient-bg4(#3B3637,#7F7873);
          width: 50%;
          border-radius: 5px;
        }
      }
    }
  }
  @media only screen and (max-width : 768px){
    // background: red;
    padding: 6px;
    border-radius: 0 0 6px 6px;

    .dice__history__table__row{
      margin-bottom: 8px;

      .dice__history__table__cell{
        font: 400 15px/18px 'Inter', sans-serif;

        span{
          &.highlight{
            width: 80px;
          }
        }
        img{
          width: 16px;
          margin-right: 5px;
        }
      }
      &.dice__history__table__row-head{
        padding: 3px 0 6px;

        .dice__history__table__cell{
          p{
            font:400 13px/16px 'Inter', sans-serif;
          }
        }
      }
    }
  }
  @media only screen and (max-width : 576px){
    // background: green;
    padding: 4px;

    .dice__history__table__row{

      .dice__history__table__cell{
        font: 400 15px/16px 'Inter', sans-serif;

        span{
          &.highlight{
            width: 75px;
            border-radius: 16px;
            font: 400 14px/16px 'Inter', sans-serif;
          }
        }
        img{
          width: 14px;
          margin-right: 2px;
        }
        &:nth-child(1){
          @include ellipsis-text;
          font: 400 11px/16px 'Inter', sans-serif;
        }
        &:nth-child(2){
          width: 80px;
        }
        &:nth-child(3){
          width: 50px;
        }
        &:nth-child(4),&:nth-child(5){
          width: calc(40% - 65px);
          font: 400 14px/16px 'Inter', sans-serif;

        }
      }
      &.dice__history__table__row-head{
        .dice__history__table__cell{
          p{
            font: 400 12px/16px 'Inter', sans-serif;
          }
        }
      }
      &.dice__history__table__row-dummy{
        .dice__history__table__cell{
          span{
            width: 70%;
            border-radius: 4px;
          }
        }
      }
    }
  }
}
