@import '../../../scss/mixin';


.referral__guide{
  @include flex-row;
  @include justify-space-between;
  align-items: stretch;

  .referral__guide__each{
    @include flex-column;
    @include gradient-bg2(#607285,#988971);
    border-radius: 10px;
    width: calc(50% - 10px);
    padding: 1px;

    .referral__guide__title{
      @include flex-column;
      @include align-center;
      flex: 0 1 auto;
      background: #18181B;
      border-radius: 9px 9px 0 0;
      margin-bottom: 1px;
      padding: 15px 5px;

      h1{
        @include display-block;
        @include gradient-text4(#96B3D0,#D5C09F,0deg);
        width: max-content;
        font: 600 22px/24px 'Inter', sans-serif;
        margin-bottom: 10px;
      }
      svg{
        @include display-block;
        width: 80px;
        height: auto;
        fill: #988971;
      }
    }
    p{
      @include display-block;
      flex: 1 1 auto;
      background: #18181B;
      color: #bbb;
      border-radius: 0 0 9px 9px;
      padding: 15px 10px;
      font:400 16px/22px 'Inter', sans-serif;
      text-align: justify;
    }
    @media only screen and (max-width : 576px){
      border-radius: 6px;
      width: calc(50% - 5px);

      .referral__guide__title{
        border-radius: 6px 6px 0 0;
        padding: 10px 5px;
        h1{
          font: 600 18px/22px 'Inter', sans-serif;
          margin-bottom: 5px;
        }
        svg{
          width: 60px;
        }
      }
      p{
        border-radius: 0 0 6px 6px;
        padding: 8px 10px;
        font:400 14px/20px 'Inter', sans-serif;
      }
    }
  }
}


.referral__link{
  @include display-block;
  @include gradient-bg2(#607285,#988971);
  @include psuedo-bg(1px,9px,#18181B);
  margin-top: 30px;
  border-radius: 10px;
  padding: 20px;

  h2{
    @include display-block;
    @include gradient-text4(#96B3D0,#D5C09F,0deg);
    width: max-content;
    font:600 18px/22px 'Inter', sans-serif;
    margin-bottom: 15px;
  }
  .referral__link__input{
    @include flex-row;
    padding: 15px;
    border-radius: 5px;
    background: #202020;
    box-shadow: inset 0 0 4px 0 #000,0 0 4px 0 #303033;
    p{
      @include display-block;
      color: #D5C09F;
      font:400 16px/18px 'Inter', sans-serif;
    }
    button{
      @include display-block;
      width: max-content;
      flex-shrink: 0;
      &.active{
        &:after{
          position: absolute;
          display: block;
          bottom: calc(100% + 12px);
          right: -6px;
          content: "copied";
          background: #333;
          padding: 6px 8px;
          border-radius: 3px;
          font: 400 16px/18px 'Inter', sans-serif;
          color:  #96B3D0;
          z-index: 1;
        }

        &:before {
          position: absolute;
          content: "";
          bottom: 100%;
          left: 50%;
          transform: translateX(-50%);
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-top: 12px solid #333;
        }
      }
      svg{
        @include display-block;
        height: 20px;
        width: auto;
        fill: #96B3D0;
      }
      &:hover{
        svg{
          fill: #fff;
        }
      }
    }
  }
  .referral__rules_btn{
    @include flex-row;
    @include justify-end;
    color: #96B3D0;
    font:400 16px/18px 'Inter', sans-serif;
    margin-top: 10px;
    cursor: pointer;
    transition-duration: 0.4s;
    transition-property: color fill;
    svg{
      @include display-block;
      height: 20px;
      width: auto;
      fill: #96B3D0;
      margin-left: 5px;
    }

    &:hover{
      color: #fff;
      svg{
        fill: #fff;
      }
    }
  }
  .referral__count{
    @include display-block;
    color: #aaa;
    font:400 16px/18px 'Inter', sans-serif;
  }

  @media only screen and (max-width : 576px){
    @include psuedo-bg(1px,6px,#18181B);
    border-radius: 6px;
    margin-top: 20px;
    padding: 15px 10px;

    h2{
      margin-bottom: 10px;
    }
    .referral__link__input{
      padding: 10px;
      border-radius: 5px;
      box-shadow: inset 0 0 2px 0 #000,0 0 3px 0 #303033;
      p{
        font:400 15px/18px 'Inter', sans-serif;
      }
    }
  }
}



.referral__rules{
  @include display-block;
  @include gradient-bg2(#607285,#988971);
  @include psuedo-bg(1px,9px,#18181B);
  margin-top: 30px;
  border-radius: 10px;
  padding: 20px;

  h1{
    @include display-block;
    @include gradient-text4(#96B3D0,#D5C09F,0deg);
    width: max-content;
    font:600 22px/24px 'Inter', sans-serif;
    margin-bottom: 15px;

  }
  ol{
    @include display-block;
    list-style: decimal inside;
    li{
      position: relative;
      width: 100%;
      padding: 0;
      font: 400 15px/22px 'Inter', sans-serif;
      color: #bbb;
      margin: 0 0 10px;
    }
  }
  p{
    @include display-block;
    font: 400 16px/18px 'Inter', sans-serif;
    color: #E33333;
    margin-bottom: 4px;
  }
  span{
    @include display-block;
    font: 400 14px/18px 'Inter', sans-serif;
    color: #aaa;
  }

  @media only screen and (max-width : 576px){
    h1{
      font:500 20px/22px 'Inter', sans-serif;
    }
  }
}
