@import '../../../scss/mixin';

.dice__prediction__and__lucky{
  @include flex-row;
  .prediction__num{
    @include display-block;
    width: 200px;
    margin-right: 5px;
    text-align: center;
    padding: 8px 0 20px;
    border-radius: 8px 0 0 8px;
    background-image: linear-gradient(180deg,#332F42 0%,#0E0E0F 100%);
    box-shadow: -4px -4px 8px 0 #100E18,-4px 4px 8px 0px #100E18,inset 0 0 8px 0 #080A14;

    &:last-child{
      margin-right: 0;
      border-radius: 0 8px 8px 0;
      box-shadow: 4px 4px 8px 0px #100E18,4px -4px 8px 0px #100E18,inset 0 0 8px 0 #080A14;

    }
    h4{
      @include display-block;
      font:600 16px/20px 'Inter', sans-serif;
      color: #bbb;
      text-shadow: 1px 1px 3px #000;
      margin-bottom: 8px;
    }
    p{
      @include display-block;
      font:600 120px/120px 'Inter', sans-serif;
      color: #ccc;
      text-shadow: 3px 3px 6px #0a0a0a;
    }
    @media only screen and (max-width : 1200px){
      width: 160px;
      p{
        font:600 90px/90px 'Inter', sans-serif;
      }
    }
    @media only screen and (max-width : 992px){
      width: 180px;
      p{
        font:600 110px/110px 'Inter', sans-serif;
      }
    }
    @media only screen and (max-width : 768px){
      width: 160px;
      p{
        font:600 90px/90px 'Inter', sans-serif;
      }
    }
    @media only screen and (max-width : 576px){
      width: 140px;
      padding: 8px 0 10px;
      border-radius: 5px 0 0 5px;
      box-shadow: -2px -2px 2px 0 #100E18,-2px -2px 4px 0px #100E18,inset 0 0 4px 0 #080A14;

      &:last-child{
        border-radius: 0 5px 5px 0;
        box-shadow: 2px 2px 4px 0px #100E18,2px -2px 4px 0px #100E18,inset 0 0 4px 0 #080A14;
      }
      h4{
        font:600 14px/18px 'Inter', sans-serif;
      }
      p{
        font:600 60px/60px 'Inter', sans-serif;
      }
    }
  }
}

.dice__account__balance{
  @include flex-row;
  @include justify-space-between;
  margin-bottom: 10px;

  p{
    @include flex-row;
    width: max-content;
    font:400 14px/20px 'Inter', sans-serif;
    color: #aaa;
    svg{
      display: block;
      width: 30px;
      height: auto;
      fill:#666;
    }

    span{
      @include display-block;
      width: max-content;
      font:500 18px/20px 'Inter', sans-serif;
      margin: 0 2px 0 8px;
      color: #ccc;
    }
  }
}



.dice__bet__info{
  @include flex-row;
  @include justify-space-between;
  flex-wrap: wrap;

  .dice__bet__info__each{
    @include display-block;
    width: calc(50% - 5px);
    text-align: center;
    background: #18191E;
    margin-bottom: 10px;
    padding: 8px 5px;

    --r : 8px;
    &:nth-child(1){
      border-radius: var(--r) 0 0 0;
    }
    &:nth-child(2){
      border-radius: 0 var(--r) 0 0;
    }
    &:nth-child(3){
      border-radius: 0 0 0 var(--r);
    }
    &:nth-child(4){
      border-radius: 0 0 var(--r) 0;
    }
    p{
      @include display-block;
      font:400 16px/20px 'Inter', sans-serif;
      color: #999;
      margin-bottom: 6px;
    }
    span{
      @include display-block;
      font:500 16px/20px 'Inter', sans-serif;
      color: #777;
    }
    @media only screen and (max-width : 576px){
      width: calc(50% - 3px);
      margin-bottom: 6px;
      padding: 5px 5px;
      --r : 5px;

      p{
        font:500 14px/16px 'Inter', sans-serif;
        margin-bottom: 4px;
      }
      span{
        font:500 14px/16px 'Inter', sans-serif;
      }
    }
  }
}
