@import '../../scss/mixin';

.navigation{
  @include flex-row;
  @include justify-space-between;
  position: fixed;
  width: 100vw;
  height: 50px;
  top: 0;
  padding: 0 20px;
  background: #272729;
  z-index: 10;

  .nav__logo{
    @include display-block;
    @include gradient-text(#E01C38,#E2F6AD);
    width: max-content;
    font:900 35px/50px 'Inter', sans-serif;
    text-decoration: none;
  }
  .nav__list{
    @include flex-row;
    width: max-content;
    height: 50px;
    li{
      @include flex-column;
      @include align-center;
      width: auto;
      margin-right: 30px;
      cursor: pointer;
      color: #aaa;

      &:last-child{
        margin-right: 0;
      }
      i{
        font-size: 22px;
      }
      p{
        @include display-block;
        margin-top: 5px;
        font:600 13px/14px 'Inter', sans-serif;
      }
      &:hover{
        color: #fff;
      }
    }
  }

  .wallet__menu{
    @include flex-row;
    width: max-content;
    height: 50px;
    .menu__btn{
      @include display-block;
      display: none;
      width: max-content;
      margin-left: 10px;
      font-size: 24px;
      width: 24px;
      color: #aaa;
      &:hover{
        color: #ddd;
      }
    }

  }


  @media only screen and (max-width : 768px){

    padding: 0 15px;

    .nav__logo{
      font:900 30px/45px 'Inter', sans-serif;
    }
    .nav__list{
      @include flex-column;
      position: absolute;
      right: -70%;
      top: 50px;
      height: calc(100vh - 50px);
      background: #272729;
      width: 60%;
      transition: right .3s;

      &.active{
        right: 0;
      }

      li{
        @include flex-row;
        padding: 10px 0 10px 20px;
        border-top: solid 1px #404042;
        &:last-child{
          border-bottom: solid 1px #404042;
        }
        i{
          font-size: 25px;
        }
        p{
          margin-top: 0;
          margin-left: 20px;
          font:500 18px/20px 'Inter', sans-serif;
        }
      }
    }

    .wallet__menu{
      .menu__btn{
        display: block;
      }
    }
  }

  @media only screen and (max-width : 576px){

    .nav__list{

      width: 70%;
      li{
        padding: 10px 0 10px 10px;
        i{
          font-size: 20px;
        }
        p{
          margin-left: 10px;
          font:500 16px/18px 'Inter', sans-serif;
        }
      }
    }
  }
  @media only screen and (max-width : 300px){
    .nav__logo{
      font:800 20px/25px 'Inter', sans-serif;
    }
    .nav__list{
      width: 90%;
      right: -90%;
    }
  }

}
