
@import '../../scss/mixin';


/*==============================
	Paginator
==============================*/
.paginator {
  @include flex-row;
  @include justify-space-between;
  background: #202020;
  border-radius: 5px;
  padding: 6px 8px 6px 10px;
  margin-top: 15px;

  .paginator__pages{
    @include display-block;
    width: max-content;
    font:400 14px/16px 'Inter', sans-serif;
    color: #ccc;
  }
  .paginator__list{
    @include flex-row;
    width: max-content;

    li {
      @include display-block;
      width: max-content;
      margin-right: 8px;
      &:last-child {
        margin-right: 0;
      }

      button {
        @include flex-row;
        width: max-content;
        min-width: 24px;
        height: 24px;
        border-radius: 5px;
        background: #283445;
        color: #ACCDEE;
        font: 400 14px/16px 'Inter', sans-serif;
        padding: 0 3px;
        svg {
          width: auto;
          height: 22px;
          fill: #ACCDEE;
          transition: fill 0.5s ease;
        }
        &:hover {
          color: #fff;
          svg {
            fill: #fff;
          }
        }
      }
      &.active button {
        background: #444A6D;
        color: #fff;
        cursor: default;
      }
      &.paginator__go{
        @include flex-row;
        input{
          @include display-block;
          text-align: center;
          background: transparent;
          border: solid 2px #283445;
          width: 40px;
          height: 24px;
          font: 400 14px/14px 'Inter', sans-serif;
          color: #ccc;
          border-radius: 5px 0 0 5px;
        }
        button{
          border-radius: 0 5px 5px 0;
          padding: 0 5px;
        }
      }
    }
  }
  @media only screen and (max-width : 576px){
    padding: 6px;
    margin-top: 10px;
    @include justify-center;

    .paginator__pages{
      display: none;
    }
    .paginator__list{
      li {
        margin-right: 6px;
        &.paginator__go{
          input{
            width: 35px;
            border: solid 1px #283445;
          }
          button{
            padding: 0 4px;
          }
        }
      }
    }
  }
}
