@import '../../../scss/mixin';




.stake__token{
  @include display-block;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  .stake__token__p{
    @include display-block;
    font:500 16px/20px 'Inter', sans-serif;
    color: #aaa;
  }

  .progress__meter{
    @include display-block;
    @include user-select-none;
    background: #151518;
    border: solid 2px #555;
    margin: 20px auto;
    border-radius: 15px;

    --w:0%;
    .progress__meter__bar{
      @include display-block;
      @include gradient-striped2(#080E15,#111C2B,40px);
      width: var(--w);
      height: 35px;
      border-radius: 14px;
      transition: width 1s;
    }
    p{
      @include display-block;
      position: absolute;
      top:50%;
      transform: translateY(-50%);
      font:400 20px/20px 'Inter', sans-serif;
      color: #ddd;
    }
  }

  .stake__pool{
    @include display-block;
    background: #151518;
    margin: 30px 0 30px;
    padding: 20px 10px;
    box-shadow: inset 0 0 5px 0 #0b0b0d;
    border-radius: 6px;
    h2{
      @include display-block;
      @include gradient-text2(#F3E784,#23A484);
      font:600 26px/30px 'Inter', sans-serif;
    }
    .stake__pool__value{
      @include display-block;
      font:600 30px/35px 'Inter', sans-serif;
      text-shadow: 0 0 4px #000;
      color: #ccc;
      margin: 15px 0 20px
    }
    .stake__pool__info{
      @include display-block;
      font:400 16px/25px 'Inter', sans-serif;
      color: #aaa;
      margin-bottom: 20px;
      span{
        @include display-block;
        display: inline;
        width: max-content;
        font:400 18px/25px 'Inter', sans-serif;
        color: #67C06F;
        margin-left: 4px;
      }
    }
    .stake__pool__timer{
      @include display-block;
      font:400 16px/22px 'Inter', sans-serif;
      color: #aaa;

      span{
        @include display-block;
        display: inline;
        width: max-content;
        font:500 16px/22px 'Inter', sans-serif;
        color: #E2A368;
      }
    }
  }
  .stake__options{
    @include flex-row;
    @include justify-space-between;
    margin-bottom: 10px;

    .stake__options__each{
      @include display-block;
      width: calc(50% - 10px);

      .stake__label{
        @include display-block;
        @include gradient-text4(#E8A561,#27C575);
        font:500 18px/20px 'Inter', sans-serif;
      }

      .stake__input{
        @include display-block;
        @include gradient-bg2(#2F425F,#02264F);
        margin: 10px 0 10px;
        padding: 2px;
        border-radius: 6px;
        input{
          @include display-block;
          font:400 20px/24px 'Inter', sans-serif;
          text-align: center;
          background: #18181B;
          color: #bbb;
          border-radius: 5px;
          padding: 8px 4px;
          &::placeholder{
            font:400 18px/24px 'Inter', sans-serif;
            color: #888;
          }
        }
      }

      .stake__button{
        @include flex-row;
        font:500 18px/20px 'Inter', sans-serif;
        color: #bbb;
        background: #1C2739;
        padding: 10px 10px;
        border-radius: 6px;
        &:hover{
          color: #fff;
          background: #25344C;
        }
      }
    }
  }

  @media only screen and (max-width: 768px){
    width: 90%;
  }
  @media only screen and (max-width: 576px){
    width: calc(100% - 10px);
    .stake__token__p{
      font:500 14px/20px 'Inter', sans-serif;
    }
    .progress__meter{
      margin: 15px auto;
      border-radius: 10px;
      .progress__meter__bar{
        border-radius: 9px;
      }
      p{
        font:400 18px/18px 'Inter', sans-serif;
      }
    }
    .stake__pool{
      margin: 20px 0 20px;
      padding: 10px 10px;
      h2{
        font: 600 24px/30px 'Inter', sans-serif;
      }
      .stake__pool__value{
        font: 500 28px/30px 'Inter', sans-serif;
        margin: 15px 0 15px
      }
      .stake__pool__info{
        // font: 400 16px/25px 'Inter', sans-serif;
        margin-bottom: 20px;
        span{
          font: 400 16px/25px 'Inter', sans-serif;
        }
      }
      .stake__pool__timer{
        font: 400 16px/20px 'Inter', sans-serif;
        span{
          font: 500 16px/20px 'Inter', sans-serif;
        }
      }
    }
    .stake__options{
      .stake__options__each{
        width: calc(50% - 5px);
        .stake__label{
          font:400 18px/20px 'Inter', sans-serif;
        }

        .stake__input{
          margin: 6px 0 10px;
          padding: 1px;
          input{
            font:400 18px/22px 'Inter', sans-serif;
            &::placeholder{
              font:400 16px/18px 'Inter', sans-serif;
            }
          }
        }
        .stake__button{
          font:400 18px/20px 'Inter', sans-serif;
          padding: 10px 4px;
        }
      }
    }
  }
}
