@import '../../scss/mixin';

body:has(.popup__modal) {
  overflow: hidden;
}

@keyframes stretch-popup {
  0% {
    opacity: 0;
    transform: scale(0.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@-webkit-keyframes stretch-popup {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.popup__modal{
  @include display-block;
  position: fixed;
  z-index: 2;
  top: 50px;
  left: 0;
  bottom: 0;
  right:0;
  overflow: hidden;
  backdrop-filter: blur(2px);

  &.popup__modal__mid{
    .popup__modal__body{
      max-width: 700px;
      margin-top: 50px;
      .popup__modal__head{
        height: 60px;
        .popup__title{
          font:700 32px/34px 'Inter', sans-serif;
        }
        .popup__close{
          top: 10px;
        }
      }
    }
  }

  &.popup__modal__full{
    .popup__modal__body{
      max-width: inherit;
      margin-top: 0;
      border-radius: 0;
      .popup__modal__head{
        border-radius: 0;
        height: 60px;
        .popup__title{
          font:700 32px/34px 'Inter', sans-serif;
        }
        .popup__close{
          top: 10px;
        }
      }
      .popup__modal__content{
        border-radius: 0;

        .popup__modal__content__body{
          max-height: calc(100vh - 156px);
        }
      }
    }
  }


  .popup__modal__body{
    @include display-block;
    @include gradient-bg2(#512338,#1B532B);
    box-shadow: 0 0 15px 2px #000;
    max-width: 480px;
    margin: 60px auto 0;
    padding: 2px;
    border-radius: 12px;
    overflow: hidden;
    animation: stretch-popup 0.4s ease-out;
    align-items: stretch;
    .popup__modal__head{
      @include flex-row;
      background: #18181B;
      border-radius: 12px 12px 0 0;
      overflow: hidden;
      margin-bottom: 2px;
      height: 50px;
      box-shadow: inset 0 0 4px #000;
      .popup__title{
        @include display-block;
        @include gradient-text(#F39D68,#3AAE5D);
        text-align: center;
        width: max-content;
        margin: 13px auto ;
        font:700 22px/24px 'Inter', sans-serif;
      }
      .popup__close{
        @include display-block;
        width: auto;
        position: absolute;
        top: 5px;
        right: 8px;
        svg {
          fill: #888;
          transition: 0.5s ease;
          height: auto;
          width: 40px;
        }
        &:hover svg {
          fill: #fff;
        }
      }
    }

    .popup__modal__content{
      @include display-block;
      box-shadow: inset 0 0 4px #000;
      padding: 20px 5px 20px 20px;
      background: #18181B;
      border-radius: 0 0 12px 12px;

      .popup__modal__content__body{
        @include display-block;
        @include scroll-bar(6px,0,0);
        max-height: calc(100vh - 236px);
        padding-right: 15px;
        overflow-x: hidden;
        overflow-y: auto;
      }
    }
  }

  @media only screen and (max-width: 768px){
    .popup__modal__body{
      width: calc(100% - 20px);
      padding: 1px;
      box-shadow: 0 0 10px 2px #000;
      margin-top: 40px;
      border-radius: 10px;

      .popup__modal__head{
        border-radius: 10px 10px 0 0;
        margin-bottom: 1px;
        .popup__title{
          margin: 15px auto ;
          font:600 18px/20px 'Inter', sans-serif;
        }
        .popup__close{
          top: 7px;
          svg {
            width: 36px;
          }
        }
      }
      .popup__modal__content{
        padding: 10px 5px 10px 10px;
        border-radius: 0 0 10px 10px;
        .popup__modal__content__body{
          @include scroll-bar(4px,0,0);
          padding-right: 5px;
          max-height: calc(100svh - 184px);
        }
      }
    }

    &.popup__modal__mid{
      .popup__modal__body{
        margin-top: 30px;

        .popup__modal__head{
          height: 50px;
          .popup__title{
            font:600 28px/30px 'Inter', sans-serif;
          }
          .popup__close{
            top: 7px;
          }
        }
        .popup__modal__content{
          .popup__modal__content__body{
            max-height: calc(100svh - 174px);
          }
        }
      }
    }


    &.popup__modal__full{
      .popup__modal__body{
        width: 100%;
        .popup__modal__head{
          height: 50px;
          .popup__title{
            font:600 28px/30px 'Inter', sans-serif;
          }
          .popup__close{
            top: 7px;
          }
        }
        .popup__modal__content{
          .popup__modal__content__body{
            max-height: calc(100svh - 124px);
          }
        }
      }
    }

  }
}
