@import '../../../scss/mixin';


.dice__range__slider{
  @include flex-row;
  width: 60%;
  margin: 50px 0 50px;
  span{
    @include display-block;
    width: max-content;
    flex-shrink: 0;
    font:400 16px/16px 'Inter', sans-serif;

  }
  input{
    @include display-block;
    flex-shrink: 1;
    margin: 0 6px;
    -webkit-appearance: none;
    height: 16px;
    background: #444;
    border-radius: 4px;
    box-shadow: 0 0 2px 6px rgba(#0a0a0a, 0.3);

    --w :50%;
    --c1 :#0BB303;
    --c2 :#BD0F03;
    // --bg : url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" height="40" width="40"><text x="9" y="28" fill="white" font-size="22" textLength="22">49</text></svg>');
    --bg : url('../../../images/numbers.png');
    --p:0;
    background: linear-gradient(to right,var(--c1) var(--w),var(--c2) var(--w));

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 40px;
      height: 40px;
      border-radius: 25px;
      cursor: pointer;
      box-shadow: 0 0 2px 6px rgba(#0a0a0a, 0.5);
      background-color: #202020;
      background-image: var(--bg);
      background-repeat: no-repeat;
      background-size: cover;
      // background-position: center;
      background-position:calc(var(--p)*-40px) 0px;
    }
  }

  @media only screen and (max-width : 1432px){
    width: 65%;
  }
  @media only screen and (max-width : 1200px){
    width: 75%;
  }
  @media only screen and (max-width : 992px){
    width: 60%;
  }
  @media only screen and (max-width : 768px){
    width: 75%;
    margin: 40px 0;
  }
  @media only screen and (max-width : 576px){
    width: calc(100% - 40px);
  }
}


.dice__roll__mode{
  @include display-block;
  @include user-select-none;
  width: 60%;

  input{
    display: block;
    visibility: hidden;
    opacity: 0;
    width: 0;
    height: 0;

    &:checked + .dice__roll__mode__body span{
      border-radius: 0 8px 8px 0;
      left: calc(50% + 1px);
    }
  }

  .dice__roll__mode__body{
    @include flex-row;
    @include justify-space-between;
    padding: 2px;
    background: #0a0a0a;
    border: solid 2px #0E1933;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: -4px -4px 8px 0 #080A14,4px 4px 8px 0px #080A14;

    p{
      @include display-block;
      width: calc(50% - 1px);
      background: #0F1214;
      font: 500 18px/26px 'Inter', sans-serif;
      text-align: center;
      color:  #aaa;
      padding: 6px 0;
      border-radius: 8px 0 0 8px;
      &:nth-child(2){
        border-radius: 0 8px 8px 0;
      }
    }
    span{
      position: absolute;
      display: block;
      width: calc(50% - 3px);
      left: 2px;
      background: #0E1933;
      font: 500 18px/26px 'Inter', sans-serif;
      text-shadow: 2px 2px 2px #000;
      text-align: center;
      border-radius: 8px 0 0 8px;
      padding: 6px 0;
      transition: left 0.3s;
    }
  }
  @media only screen and (max-width : 1432px){
    width: 65%;
  }
  @media only screen and (max-width : 1200px){
    width: 75%;
  }
  @media only screen and (max-width : 992px){
    width: 60%;
  }
  @media only screen and (max-width : 768px){
    width: 75%;
  }
  @media only screen and (max-width : 576px){
    width: calc(100% - 60px);
  }
}


.bet__amount__btns{
  @include flex-row;
  @include justify-start;
  margin-bottom: 10px;
  li{
    @include display-block;
    cursor: pointer;
    width: 30px;
    background: #121e38;
    margin-right: 5px;
    text-align: center;
    padding: 4px 0;
    font:400 12px/14px 'Inter', sans-serif;
    color: #bbb;
    &:hover{
      background: #0E1933;
    }
    &:first-child{
      border-radius: 4px 0 0 4px;
    }
    &:last-child{
      margin-right: 0;
      border-radius: 0 4px 4px 0;
    }
  }
}


.bet__amount__input{
  @include display-block;
  // margin-bottom: 20px;

  span{
    @include display-block;
    font: 400 16px/18px 'Inter', sans-serif;
    color: #aaa;
    margin-bottom: 8px;
  }

  .input{
    @include flex-row;
    border: solid 2px #0E1933;
    border-radius: 6px;
    padding: 4px;
    svg{
      display: block;
      height: 36px;
      width: auto;
      flex-shrink: 0;
      path{
        fill: #0E1933;
      }
    }
    input{
      @include display-block;
      font: 500 24px/28px 'Inter', sans-serif;
      background: transparent;
      color: #aaa;
      flex-shrink: 1;
      padding: 0 10px 0 10px;
    }
  }
}

.auto__bet__toggler{
  @include flex-row;
  @include justify-start;
  span{
    @include display-block;
    width: max-content;
    font: 400 14px/16px 'Inter', sans-serif;
    color:  #999;
  }
  label{
    @include display-block;
    width: max-content;
    margin: 0 5px 0 10px;
    input{
      display: block;
      visibility: hidden;
      opacity: 0;
      width: 0;
      height: 0;

      &:checked + span{
        border-color: #0E1933;
        &:before {
          content: "ON";
          background: #0E1933;
          border-radius: 0 4px 4px 0;
          -webkit-transform: translateX(100%);
          -ms-transform: translateX(100%);
          transform: translateX(100%);
        }
      }
    }
    span{
      @include flex-row;
      @include justify-start;
      width: 70px;
      height: 28px;
      padding: 2px;
      border: solid 2px #121e38;
      border-radius: 6px;
      cursor: pointer;
      &:before {
        @include display-block;
        content: "OFF";
        width: 50%;
        background: #121e38;
        font: 400 12px/14px 'Inter', sans-serif;
        text-align: center;
        color:  #aaa;
        padding: 3px 0;
        border-radius: 4px 0 0 4px;
        transition: .3s;
      }
    }
  }
  .auto__bet__info{
    @include display-block;
    width: max-content;
    i{
      color:  #333;
      font-size: 16px;
      cursor: pointer;
      &:hover + p{
        display: block;
      }
    }
    P{
      display: none;
      position: absolute;
      width: 220px;
      left: -110px;
      background: #333;
      padding: 6px 8px;
      border-radius: 5px;
      font: 400 14px/16px 'Inter', sans-serif;
      text-align: left;
      color:  #ccc;
      z-index: 1;
      &:before {
        position: absolute;
        content: "";
        bottom: 100%;
        left: 111px;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-bottom: 14px solid #333;
      }
    }
  }
}
