@import '../../scss/mixin';


@-webkit-keyframes reduce-width {
  from { width: 100%}
  to { width: 0%}
}
@keyframes reduce-width {
  from { width: 100%}
  to { width: 0%}
}

@-webkit-keyframes show_toast {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes show_toast {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes hide_toast {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(calc(100% + 10px));
            transform: translateX(calc(100% + 10px));
  }
}
@keyframes hide_toast {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(calc(100% + 10px));
            transform: translateX(calc(100% + 10px));
  }
}

#toast-alert{
  @include display-block;
  position: fixed;
  max-width: 350px;
  top: 55px;
  right: 5px;
  z-index: 10;

  ul{
    @include display-block;
    $success: #22bb33;
    $error: #bb2124;
    $warning: #f0ad4e;
    $info: #3498DB;

    .toast {
      @include flex-row;
      @include justify-space-between;
      @include align-start;
      align-items: stretch;
      list-style: none;
      margin-bottom: 10px;
      background: #303030;
      animation: show_toast 0.3s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
      &.hide {
        animation: hide_toast 0.3s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
      }
      &:last-child{
        margin-bottom: 0px;
      }

      span{
        @include flex-row;
        flex-shrink: 0;
        width: 30px;
        i{
          @include display-block;
          width: auto;
          text-shadow: 0 0 2px #000;
          font-size: 16px;
        }
      }
      p{
        @include display-block;
        flex-shrink: 1;
        color: #ccc;
        font: 400 16px/24px 'Inter', sans-serif;
        padding: 10px 0 10px 6px;
        &::before {
          position: absolute;
          content: "";
          height: 2px;
          width: 100%;
          bottom: 0px;
          left: 0px;
          @include animation(reduce-width 5s linear forwards);
        }
      }
      button{
        @include display-block;
        width: max-content;
        flex-shrink: 0;
        margin: 0 3px 0 4px;
        i{
          @include display-block;
          font-size: 20px;
          color: #aaa;
          &:hover{
            color: #fff;
          }
        }
      }

      &.success{
        span {background: $success;i{color: #6EFA98;}}
        p::before {
          background: $success;
        }
      }
      &.error{
        span {background: $error;i{color: #ED5B5B;}}
        p::before {
          background: $error;
        }
      }
      &.warning{
        span {background: $warning;i{color: #FAD672;}}
        p::before {
          background: $warning;
        }
      }
      &.info{
        span {background: $info;i{color: #8DBEF2;}}
        p::before {
          background: $info;
        }
      }
    }
  }

  @media screen and (max-width: 768px){
    max-width: 300px;
    ul{
      .toast {
        margin-bottom: 8px;
        span{
          width: 26px;
          i{
            font-size: 14px;
          }
        }
        p{
          font: 400 15px/22px 'Inter', sans-serif;
          padding: 6px 0 6px 6px;
        }
        button{
          margin: 0 2px 0 2px;
          i{
            font-size: 16px;
          }
        }

      }
    }
  }
}
