@import '../../../scss/mixin';

// @include gradient-bg2(#2F425F,#02264F);

.signup__group{
  @include display-block;
  margin-bottom: 15px;

  label{
    @include display-block;
    @include gradient-text2(#46EB80,#F31A46,225deg);
    width: max-content;
    font:500 16px/18px 'Inter', sans-serif;
    margin-bottom: 5px;
  }
  input{
    @include display-block;
    font: 400 16px/18px 'Inter', sans-serif;
    background: #18181B;
    color: #ccc;
    border: solid 2px #2F425F;
    border-radius: 5px;
    padding: 8px 10px;
    &:hover,&:focus{
      border-color: #02264F;
    }
  }
}

.signup__terms{
  @include display-block;
  margin-bottom: 10px;
  input{
    display: block;
    visibility: hidden;
    opacity: 0;
    width: 0;
    height: 0;
    &:checked + label{
      svg{
        fill: #9BF4EB;
      }
    }
  }
  label{
    @include display-block;
    display: inline-block;
    vertical-align: middle;
    width: max-content;
    cursor: pointer;
    background: #222;
    border: solid 2px #436A66;
    border-radius: 5px;
    margin-right: 6px;

    svg{
      display: block;
      position: relative;
      width: 18px;
      height: auto;
      fill: #666;
    }
    &:hover{
      svg{
        fill: #ccc;
      }
    }
  }
  span{
    @include display-block;
    display: inline;
    width: max-content;
    font: 400 14px/18px 'Inter', sans-serif;
    color: #aaa;

    a{
      @include display-block;
      display: inline;
      width: max-content;
      color: #aaa;

    }
  }
}
