@import '../../../scss/mixin';



.wallet__not__installed{
  @include display-block;

  p{
    @include display-block;
    font:400 16px/25px 'Inter', sans-serif;
    color: #ccc;
    margin-bottom: 20px;
  }
  .app__extension{
    @include display-block;
    width: max-content;
    margin: 0 auto;
    font:600 16px/20px 'Inter', sans-serif;
    color: #ff3e30;
  }

  .or__btn{
    @include display-block;
    margin: 15px 0;
    span{
      @include flex-row;
      width: 40px;
      aspect-ratio: 1;
      font:600 16px/20px 'Inter', sans-serif;
      border: solid 2px #555;
      border-radius: 50%;
      color: #888;
      margin: 0 auto;
    }
  }
  .app__store__img{
    @include flex-row;
    a{
      @include display-block;
      width: 40%;
      &:first-child{
        margin-right: 10px;
      }
      img{
        @include display-block;
        box-shadow: 0 0 4px 0 #000;
      }
    }
  }
  span{
    @include display-block;
    font:400 14px/18px 'Inter', sans-serif;
    color: #aaa;
    margin-top: 20px;
  }

  @media only screen and (max-width : 576px){
    p{
      font:400 16px/22px 'Inter', sans-serif;
    }
    .app__extension{
      font:500 16px/20px 'Inter', sans-serif;
    }

    .app__store__img{
      a{
        width: calc(50% - 5px);
      }
    }
  }
}

.login__wallet{
  @include flex-column;
  @include align-center;
  img{
    @include display-block;
    width: 60%;
    object-fit: cover;
  }
  span{
    @include display-block;
    font:400 16px/20px 'Inter', sans-serif;
    color: #ccc;
    text-align: center;
    margin-top: 40px;
  }
  @media only screen and (max-width : 576px){
    img{
      width: 70%;
    }
    span{
      font:400 14px/18px 'Inter', sans-serif;
      margin-top: 20px;
    }
  }
}
